
import gsap from "gsap";
import { ScrollTrigger } from "gsap/ScrollTrigger";
import { SplitText } from "gsap/SplitText";
gsap.registerPlugin(ScrollTrigger, SplitText);

export const animate = () => {

    let point = "center";
    const sm = window.matchMedia('(max-width: 576px)');
    if (sm.matches) {
      point = "75%";
    }


  document.querySelectorAll('.chapter .h1').forEach((heading) => {
    // SplitText:   ,  <mark>  <span>   subheader    
    // 1.   <mark>  <span class="subheader">  
    const specialNodes = [];
    let idx = 0;
    heading.querySelectorAll('mark, span.subheader').forEach((node) => {
      const placeholder = `__SPECIAL_PLACEHOLDER_${idx}__`;
      specialNodes.push({ placeholder, html: node.outerHTML });
      node.replaceWith(placeholder);
      idx++;
    });

    // 2. SplitText  
    const split = new SplitText(heading, { type: 'words, lines', linesClass: 'split-line', wordsClass: 'split-word' });
    let words = split.words;

    // 3.  <mark>  <span>      words
    specialNodes.forEach(({ placeholder, html }) => {
      const widx = words.findIndex(w => w.textContent === placeholder);
      if (widx !== -1) {
        const temp = document.createElement('div');
        temp.innerHTML = html;
        const node = temp.firstChild;
        words[widx].replaceWith(node);
        words[widx] = node;
      }
    });

    // 4.   (: .word   display: inline-block  css)
    //  mark  span.subheader    display: inline-block  transform
    words.forEach(w => {
      if (
        (w.tagName && w.tagName.toLowerCase() === 'mark') ||
        (w.tagName && w.tagName.toLowerCase() === 'span' && w.classList.contains('subheader'))
      ) {
        w.style.display = 'inline-block';
      }
      gsap.set(w, { opacity: 0, y: '100%', rotation: 0, transformOrigin: 'center' });
    });

    // 5. ScrollTrigger      
    ScrollTrigger.create({
      trigger: heading,
      start: `top ${point}`,
      onEnter: () => {
        gsap.killTweensOf(words); //   ,  
        gsap.to(words, {
          opacity: 1,
          y: 0,
          rotation: 0,
          stagger: 0.05,
          duration: 1,
          ease: 'back',
        });
      },
      onLeaveBack: () => {
        gsap.killTweensOf(words); //   ,  
        gsap.to(words, {
          opacity: 0,
          y: '100%',
          rotation: 0,
          stagger: {
            each: 0.075,
            from: 'end',
          },
          duration: 0.2,
          ease: 'power1.in',
        });
      },
    });

    //    <mark>
    heading.querySelectorAll('mark').forEach((mark) => {
      if (!mark.querySelector('.underline-anim')) {
        mark.insertAdjacentHTML('beforeend', '<span class="underline-anim"></span>');
      }
      const underline = mark.querySelector('.underline-anim');
      underline.style.position = 'absolute';
      underline.style.left = 0;
      underline.style.right = 0;
      underline.style.bottom = '0.1em';
      underline.style.height = '2.5px';
      underline.style.width = '100%';
      underline.style.background = 'currentColor';
      underline.style.borderRadius = '1px';
      underline.style.transformOrigin = 'left';
      underline.style.transform = 'scaleX(0)';
      underline.style.pointerEvents = 'none';
      underline.style.display = 'block';
      underline.style.zIndex = 2;

      // ScrollTrigger      
      ScrollTrigger.create({
        trigger: heading,
        start: `top ${point}`,
        onEnter: () => {
          gsap.to(underline, {
            scaleX: 1,
            duration: 0.5,
            ease: 'power2.out',
            delay: 0.2 + words.length * 0.08,
          });
        },
        onLeaveBack: () => {
          gsap.to(underline, {
            scaleX: 0,
            duration: 0.3,
            ease: 'power1.in',
          });
        },
      });
    });

    gsap.set([
      `.h1`,
      `.h2`,
    ], { opacity: 1 })


    
  });









  document.querySelectorAll('.chapter .h2, .outtro-p').forEach((heading) => {
    // SplitText:   ,  <mark>  <span>   subheader    
    // 1.   <mark>  <span class="subheader">  
    const specialNodes = [];
    let idx = 0;
    heading.querySelectorAll('mark, span.subheader').forEach((node) => {
      const placeholder = `__SPECIAL_PLACEHOLDER_${idx}__`;
      specialNodes.push({ placeholder, html: node.outerHTML });
      node.replaceWith(placeholder);
      idx++;
    });

    // 2. SplitText  
    const split = new SplitText(heading, { type: 'words', wordsClass: 'split-word' });
    let words = split.words;

    // 3.  <mark>  <span>      words
    specialNodes.forEach(({ placeholder, html }) => {
      const widx = words.findIndex(w => w.textContent === placeholder);
      if (widx !== -1) {
        const temp = document.createElement('div');
        temp.innerHTML = html;
        const node = temp.firstChild;
        words[widx].replaceWith(node);
        words[widx] = node;
      }
    });

    // 4.   (: .word   display: inline-block  css)
    //  mark  span.subheader    display: inline-block  transform
    words.forEach(w => {
      if (
        (w.tagName && w.tagName.toLowerCase() === 'mark') ||
        (w.tagName && w.tagName.toLowerCase() === 'span' && w.classList.contains('subheader'))
      ) {
        w.style.display = 'inline-block';
      }
      gsap.set(w, { opacity: 0, y: 30, rotation: "random(-20, 20)", transformOrigin: 'center' });
    });

    // 5. ScrollTrigger      
    ScrollTrigger.create({
      trigger: heading,
      start: `top ${point}`,
      onEnter: () => {
        gsap.killTweensOf(words); //   ,  
        gsap.to(words, {
          opacity: 1,
          y: 0,
          rotation: 0,
          stagger: 0.05,
          duration: 1,
          ease: 'back',
        });
      },
      onLeaveBack: () => {
        gsap.killTweensOf(words); //   ,  
        gsap.to(words, {
          opacity: 0,
          y: 30,
          rotation: "random(-20, 20)",
          stagger: {
            each: 0.075,
            from: 'end',
          },
          duration: 0.2,
          ease: 'power1.in',
        });
      },
    });

    //    <mark>
    heading.querySelectorAll('mark').forEach((mark) => {
      if (!mark.querySelector('.underline-anim')) {
        mark.insertAdjacentHTML('beforeend', '<span class="underline-anim"></span>');
      }
      const underline = mark.querySelector('.underline-anim');
      underline.style.position = 'absolute';
      underline.style.left = 0;
      underline.style.right = 0;
      underline.style.bottom = '0.1em';
      underline.style.height = '2.5px';
      underline.style.width = '100%';
      underline.style.background = 'currentColor';
      underline.style.borderRadius = '1px';
      underline.style.transformOrigin = 'left';
      underline.style.transform = 'scaleX(0)';
      underline.style.pointerEvents = 'none';
      underline.style.display = 'block';
      underline.style.zIndex = 2;

      // ScrollTrigger      
      ScrollTrigger.create({
        trigger: heading,
        start: `top ${point}`,
        onEnter: () => {
          gsap.to(underline, {
            scaleX: 1,
            duration: 0.5,
            ease: 'power2.out',
            delay: 0.2 + words.length * 0.08,
          });
        },
        onLeaveBack: () => {
          gsap.to(underline, {
            scaleX: 0,
            duration: 0.3,
            ease: 'power1.in',
          });
        },
      });
    });

    gsap.set([
      `.h1`,
      `.h2`,
    ], { opacity: 1 })


    
  });
};

export const animateHeaders = () => {
  //   animateHeaders      layout
  document.fonts.ready.then(() => {
      // requestAnimationFrame(() => {
      //     setTimeout(() => {
      //         animate();
      //     }, 30);
      // });
      animate();
  });
}
