import { gsap } from "gsap";
import { ScrollTrigger } from "gsap/ScrollTrigger";
import { DrawSVGPlugin } from "gsap/DrawSVGPlugin";

gsap.registerPlugin(ScrollTrigger, DrawSVGPlugin);


export const animateLines = () => {
    // console.log('animateLines');
  //   .line  
  const lines = document.querySelectorAll('.line');
  
  if (!lines.length) return;

  //   path  .line  0%
  const paths = [];
  lines.forEach(line => {
    const path = line.querySelector('path');
    if (path) {
      gsap.set(path, { drawSVG: '0% 0%' });
      paths.push(path);
    }
  });

  if (!paths.length) return;




  paths.forEach((path, i) => {
    let point = "center";
    const sm = window.matchMedia('(max-width: 576px)');
    const line = lines[i];
    const dataId = line?.dataset?.id ? Number(line.dataset.id) : i;

    if (dataId === 13) point = "center-=50";
    if (dataId === 8 || dataId === 9 || dataId === 10) point = "65%";
    if (sm.matches) {
      point = "75%";
      if (dataId === 13) point = "75%-=50";
    }

    ScrollTrigger.create({
      trigger: lines[i],
      start: () => `top ${point}`,
      end: () => `bottom ${point}`,
      scrub: true,
      onUpdate: self => {
        const progress = self.progress.toFixed(2);
        gsap.to(path, {
          drawSVG: `0 ${progress * 100}%`,
          duration: 0.1,
          ease: "none"
        });
      },
      markers: false,
    });
  });
}